import React, { useEffect, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import axios from 'axios';

const Application = () => {
  const { id } = useParams();
  const [applicationData, setApplicationData] = useState(null);
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    const fetchData = async () => {
      const token = localStorage.getItem("_auth_token");
      if (!token) {
        localStorage.clear();
        navigate("/");
      }
      try {
        setLoading(true);
        const { data } = await axios.get(
          `https://campusroot.com/api/v1/counsellor/application/${id}`,
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        );
        setLoading(false);
        setApplicationData(data.data);
      } catch (error) {
        setLoading(false);
        console.error('Error fetching student data:', error.response.data.message);
      }
    };
    fetchData();
  }, []);

  return (
    <div className="container">
      {loading ? (
        <p>Loading...</p>
      ) : applicationData ? (
        <div>
          <h2>Application Details</h2>
          <div className="card">
            <div className="card-header">
              <h3>{applicationData.user.firstName} {applicationData.user.lastName}</h3>
              <p>Email: {applicationData.user.email}</p>
            </div>
            <div className="card-body">
              <p><strong>University:</strong> {applicationData.university.name}</p>
              <p><strong>Location:</strong> {applicationData.university.location.city}, {applicationData.university.location.state}, {applicationData.university.location.country}</p>
              <p><strong>Course:</strong> {applicationData.course.name} - {applicationData.course.subDiscipline}</p>
              <p><strong>Study Level:</strong> {applicationData.course.studyLevel}</p>
              <p><strong>Duration:</strong> {applicationData.course.duration} months</p>
              <p><strong>Established Year:</strong> {applicationData.university.establishedYear}</p>
              <p><strong>Type:</strong> {applicationData.university.type}</p>
              <p><strong>Intake:</strong> {new Date(applicationData.intake).toLocaleDateString()}</p>
              <p><strong>Status:</strong> {applicationData.status}</p>
              <p><strong>Stage:</strong> {applicationData.stage}</p>
              <p><strong>Cancellation Requested:</strong> {applicationData.cancellationRequest ? 'Yes' : 'No'}</p>
              <p><strong>Deadline:</strong> {applicationData.deadline ? new Date(applicationData.deadline).toLocaleDateString() : 'Not specified'}</p>
            </div>
          </div>
        </div>
      ) : (
        <p>No application data found</p>
      )}
    </div>
  );
};

export default Application;
