/* eslint-disable */
import React, { useState } from "react";
import {
  Box,
  Grid,
  Button,
  Input,
  InputAdornment,
  Typography,
  TextField,
  CircularProgress,
} from "@mui/material";
import { Email, Lock } from "@mui/icons-material";
import { useNavigate } from "react-router-dom";
import { Formik, Form, ErrorMessage } from "formik";
import * as Yup from "yup";
import Images from "../../assets";
import { authenticateLogin } from "../services";

const Login = () => {
  const isLoggedIn = localStorage.getItem("_auth_token");
  const navigate = useNavigate();
  const [error, setError] = useState();
  const [loading, setLoading] = useState(false);
  const googleLogin = localStorage.getItem("googleLogin");
  const [loginOrSignup, setLoginOrSignup] = useState("login");
  const [otpEmail, setOtpEmail] = useState("");

  const loginSchema = Yup.object().shape({
    password: Yup.string()
      .min(3, "Too Short!")
      .max(50, "Too Long!")
      .required("Required"),
    email: Yup.string()
      .matches(
        /([a-z0-9]+)([_.-])?([a-zA-Z0-9]+)@([a-zA-Z0-9]+)([.])([a-z.]+)/g,
        "Enter valid Email"
      )
      .required("Enter Email ID")
      .max(40, "Email ID must be at most 40 characters"),
});
  const submit = async (values: any) => {
    setLoading(true);
    const response = await authenticateLogin(values);
    if (response.data.success) {
      localStorage.setItem("_auth_token", response?.data?.data?.AccessToken);
      localStorage.setItem("role", response?.data?.data?.role);
      navigate(
        response.data && response?.data?.data?.role === "counsellor"
          ? "/counsellor"
          : response?.data?.data?.role === "processCoordinator"
          ? "/coordinator"
          : "/" 
      );
      setLoading(false);
    } else {
    }

    setLoading(false);
  };

  return (
    <>
      <Box
        sx={{
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          boxShadow: "2px 0px 5px 2px rgba(0, 0, 0, 0.2)",
          width: "100%",
          height: "100vh",
        }}
      >
        <Grid container>
          <Grid item xs={12} sm={5} md={7} lg={7}>
            <Box
              sx={{
                backgroundColor: "#AA96DA",
                // height:"100vh"
              }}
            >
              <img src={Images.counsellor} alt="counsellor-illustration" />
            </Box>
          </Grid>
          <Grid item xs={12} sm={7} md={5} lg={5}>
            <Box className="form">
              <Grid container>
                <Grid item xs={1} sm={2} md={2} lg={2} xl={3}></Grid>
                <Grid
                  item
                  container
                  spacing={3}
                  xs={10}
                  sm={8}
                  md={8}
                  lg={8}
                  xl={6}
                >
                  <Grid item xs={12}>
                    <Box className="form-header">
                      <Typography variant="h4" gutterBottom>
                        Welcome
                      </Typography>
                      <Typography component={"p"} gutterBottom>
                        Please enter your registered email and password to login
                      </Typography>
                    </Box>
                  </Grid>
                  <Formik
                    initialValues={{ email: "", password: "" }}
                    validationSchema={loginSchema}
                    onSubmit={(values) => submit(values)}
                  >
                    {({ isSubmitting, values, handleChange }) => {
                      return (
                        <Form>
                          <Grid container>
                            <Grid item xs={12}>
                              <TextField
                                fullWidth
                                variant="outlined"
                                className="form-input"
                                placeholder="Email"
                                name="email"
                                value={values.email}
                                onChange={handleChange}
                              />
                              <ErrorMessage name="email" component="div" />
                            </Grid>
                            <Grid item xs={12} sx={{ mt: 2 }}>
                              <TextField
                                fullWidth
                                variant="outlined"
                                className="form-input"
                                placeholder="Password"
                                type="password"
                                name="password"
                                value={values.password}
                                onChange={handleChange}
                              />
                              <ErrorMessage name="password" component="div" />
                            </Grid>
                            <Grid
                              item
                              xs={12}
                              sx={{ display: "flex", justifyContent: "end" }}
                            >
                              <Typography
                                onClick={() => setLoginOrSignup("forgot")}
                              >
                                Forgot password
                              </Typography>
                            </Grid>
                            <Grid item xs={12}>
                              <Button
                                className="form-btn"
                                variant="contained"
                                type="submit"
                              >
                                Login
                                {/* {loading ? <CircularProgress /> : <>Login</>} */}
                              </Button>
                            </Grid>
                          </Grid>
                        </Form>
                      );
                    }}
                  </Formik>
                </Grid>
              </Grid>
            </Box>
          </Grid>
        </Grid>
      </Box>
    </>
  );
};

export default Login;
