import React, { useEffect, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import axios from 'axios';

const StudentProfile = () => {
  const { id } = useParams();
  const [studentData, setStudentData] = useState(null);
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    const fetchData = async () => {
      const token = localStorage.getItem("_auth_token");
      if (!token) {
        localStorage.clear();
        navigate("/");
      }
      try {
        setLoading(true);
        const { data } = await axios.get(
          `https://campusroot.com/api/v1/counsellor/single-student/${id}`,
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        );
        setLoading(false);
        setStudentData(data.data);
      } catch (error) {
        setLoading(false);
        console.error('Error fetching student data:', error.response.data.message);
      }
    };
    fetchData();
  }, []);

  return (
    <div className="container">
      {loading && <p>Loading...</p>}
      {studentData && (
        <div>
          <div className="profile-header">
            <img src={studentData.displayPicSrc} alt="Profile" />
            <h2>{studentData.firstName} {studentData.lastName}</h2>
            <p>Email: {studentData.email}</p>
            <p>Phone: {studentData.phone.countryCode} {studentData.phone.number}</p>
          </div>
          <div className="profile-details">
            <h3>Personal Details</h3>
            <p>Date of Birth: {new Date(studentData.personalDetails.DOB).toLocaleDateString()}</p>
            <p>Gender: {studentData.personalDetails.Gender}</p>
            <p>Nationality: {studentData.personalDetails.nationality}</p>
            <p>Marital Status: {studentData.personalDetails.maritalStatus}</p>
            {/* Add more personal details as needed */}
          </div>
          <div className="education">
            <h3>Education</h3>
            {studentData.education && (
              <div>
                {/* Display school education */}
                <h4>School Education</h4>
                <p>Institute Name: {studentData.education.school.instituteName}</p>
                <p>City: {studentData.education.school.city}</p>
                <p>State: {studentData.education.school.state}</p>
                {/* Add more school education details as needed */}

                {/* Display higher secondary education */}
                <h4>Higher Secondary Education</h4>
                <p>Institute Name: {studentData.education.plus2.instituteName}</p>
                <p>City: {studentData.education.plus2.city}</p>
                <p>State: {studentData.education.plus2.state}</p>
                {/* Add more higher secondary education details as needed */}

                {/* Display undergraduate education */}
                <h4>Undergraduate Education</h4>
                <p>Institute Name: {studentData.education.underGraduation.instituteName}</p>
                <p>City: {studentData.education.underGraduation.city}</p>
                <p>State: {studentData.education.underGraduation.state}</p>
                {/* Add more undergraduate education details as needed */}

                {/* Display postgraduate education */}
                <h4>Postgraduate Education</h4>
                <p>Institute Name: {studentData.education.postGraduation.instituteName}</p>
                <p>City: {studentData.education.postGraduation.city}</p>
                <p>State: {studentData.education.postGraduation.state}</p>
                {/* Add more postgraduate education details as needed */}
              </div>
            )}
          </div>
          <div className="documents">
            <h3>Documents</h3>
            {studentData.documents && (
              <div>
                {/* Display personal documents */}
                <h4>Personal Documents</h4>
                {studentData.documents.personal && Object.keys(studentData.documents.personal).length > 0 && (
                  <ul>
                    {/* Iterate through personal documents */}
                    {Object.keys(studentData.documents.personal).map((docType) => (
                      <li key={docType}>
                        {studentData.documents.personal[docType] && (
                          <a href={studentData.documents.personal[docType].name} download>
                            {docType}
                          </a>
                        )}
                      </li>
                    ))}
                  </ul>
                )}

                {/* Display academic documents */}
                <h4>Academic Documents</h4>
                {studentData.documents.academic && Object.keys(studentData.documents.academic).length > 0 && (
                  <ul>
                    {/* Iterate through academic documents */}
                    {Object.keys(studentData.documents.academic).map((eduLevel) => (
                      <li key={eduLevel}>
                        {studentData.documents.academic[eduLevel] && (
                          <a href={studentData.documents.academic[eduLevel].name} download>
                            {eduLevel}
                          </a>
                        )}
                      </li>
                    ))}
                  </ul>
                )}
                {/* Display test documents */}
                <h4>Test Documents</h4>
                {studentData.documents.test && (
                  <ul>
                    {/* Iterate through test documents */}
                    {Object.keys(studentData.documents.test.length > 0).map((testType) => (
                      <li key={testType}>
                        <a href={studentData.documents.test[testType].name} download>
                          {testType}
                        </a>
                      </li>
                    ))}
                  </ul>
                )}

                {/* Display work experience documents */}
                <h4>Work Experience Documents</h4>
                {studentData.documents.workExperiences.length > 0 && (
                  <ul>
                    {/* Iterate through work experience documents */}
                    {studentData.documents.workExperiences.map((workExp) => (
                      <li key={workExp._id}>
                        <a href={workExp.name} download>
                          {workExp.name}
                        </a>
                      </li>
                    ))}
                  </ul>
                )}
              </div>
            )}
          </div>
          <div className="activity">
            <h3>Activity</h3>
            {studentData.activity && (
              <div>
                {/* Display application status */}
                <h4>Application Status</h4>
                {studentData.activity.applications && (
                  <ul>
                    {/* Iterate through application statuses */}
                    {studentData.activity.applications.processing.map((application) => (
                      <li key={application._id}>
                        <p>University: {application.university.name}</p>
                        <p>Status: {application.status}</p>
                        {/* Add more application status details as needed */}
                      </li>
                    ))}
                  </ul>
                )}

                {/* Display meetings */}
                {/* <h4>Meetings</h4> */}
                {
                // studentData.activity.meetings && (
                //   <ul>
                //     {/* Iterate through meetings */}
                //     {studentData.activity.meetings.map((meetingId) => (
                //       <li key={meetingId}>
                //         {/* Display meeting details */}
                //       </li>
                //     ))}
                //   </ul>
                // )
                }

                {/* Display shortlisted universities */}
                <h4>Shortlisted Universities</h4>
                {studentData.activity.shortListed && (
                  <ul>
                    {/* Iterate through shortlisted universities */}
                    {studentData.activity.shortListed.map((university) => (
                      <li key={university._id}>
                        {/* Display shortlisted university details */}
                      </li>
                    ))}
                  </ul>
                )}
              </div>
            )}
          </div>

          <div className="preference">
            <h3>Preferences</h3>
            {studentData.preference && (
              <div>
                <p>Degree: {studentData.preference.degree}</p>
                {/* Display preferred courses */}
                <h4>Preferred Courses</h4>
                <ul>
                  {studentData.preference.courses.map((course) => (
                    <li key={course}>{course}</li>
                  ))}
                </ul>
                {/* Display preferred countries */}
                <h4>Preferred Countries</h4>
                <ul>
                  {studentData.preference.country.map((country) => (
                    <li key={country}>{country}</li>
                  ))}
                </ul>
                <p>Currency: {studentData.preference.currency}</p>
                <p>Language: {studentData.preference.language}</p>
              </div>
            )}
          </div>

        </div>
      )}
    </div>
  );
};

export default StudentProfile;



